
.location {
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    
}

.location__info-left{
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 100%;
    height: 100%;
    margin-left: 100px;
    @media screen and (max-width: 425px){
        margin-left: 10px;
        max-width: 305px;
        
    }
}

.location__title{
    font-weight: 500;
    font-size: 18px;
    line-height: 25.67px;
    color: #FF6060;
    margin: 15px 0 5px;
}

.location__location{
    font-weight: 500;
    font-size: 14px;
    line-height: 142.6%;
    color: #FF6060;
    margin: 0 0 10px;
}

.location__tags{
    display: flex;
    flex-flow: wrap;
    justify-content: left;
    width: 100%;
    gap: 10px;
    @media screen and (max-width: 425px){
       
          
      }
}

.location__info-right{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    margin-right: 100px;
    margin-left: 100px;
    @media screen and (max-width: 425px){
      margin-right: 10px;
      margin-left: 10px;
      align-items: inherit;
        
    }
}

.location__host{
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: flex-end;
    margin: 15px 0;
}

.location__host img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.location__host h3{
    font-weight: 500;
    font-size: 12px;
    line-height: 17.11px;
    display: flex;
    align-items: flex-end;
    text-align: right;
    width: min-content;
    margin: 0;
    color: #FF6060;
}

.location__info{
    display: flex;
    flex-direction: column;
    gap: 45px;
    margin-left: 100px;
    margin-right: 100px;
    .dropdown__content--value {
        color: $color-1;
        font-size: 16px;
    }
    @media screen and (max-width: 425px){
        .dropdown__content--value {
            font-size: 12px;
        }
        margin: 10px;  
        gap:20px;
    
    .dropdown__header{
        height:30px;
        border-radius: 5px;
    }
    .dropdown__title{
        font-size: 14px;
    }
    }
}

@media screen and (min-width: 768px){ 
    .location{
        flex-direction: row;
        align-items: center;
        margin-left: 5px;
    }

    .location__info{
        margin-top: 25px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .location__info-right{
        flex-direction: column;
        align-items: flex-end;
    }

}

@media screen and (min-width: 1024px){
    .location__title{
        font-size: 21px;
        margin: 30px 0 0;
        white-space: nowrap;
    }
    
    .location__location{
        font-size: 18px;
        margin: 0 0 20px;
    }
    
    .location__host{
        margin: 25px 0;
    }
    
    .location__host img{
        width: 64px;
        height: 64px;
    }
    
    .location__host h3{
        font-size: 18px;
    }

 

}